import React, { Component } from 'react';
import './css/LogoClass.css';
import LogoPicture from '../res/logo.png';
import Grid from '@material-ui/core/Grid';

class LogoClass extends Component {
  render() {
    return (
        <Grid className="logoClass">
            <img src={LogoPicture} alt="BBB Auto School Logo"/>
        </Grid>
    );
  }
}

export default LogoClass;
