import React, { Component } from 'react';

import FourContacts from '../components/FourContacts';
import Jumbotron from '../components/Jumbotron';
import JumboTronContact from '../res/behind_mirror.jpg';

import './css/Contact.css';

class Contact extends Component {
  render() {
    return (
      <div>
        <div>
          <Jumbotron imageSource={JumboTronContact}/>
          <FourContacts />
      </div>
      <hr width="0"/>
    </div>
    );
  }
}

export default Contact;
