import React, { Component } from 'react';
import './css/Navigation.css';
import { Link } from 'react-router-dom';
import Button from '@material-ui/core/Button';


class App extends Component {
  render() {
    return (
      <div>
        <div className="logo">
          <Link to='/'>
            <Button color="secondary" variant="contained">BBB Auto School</Button>
          </Link>
        </div>
        <div className="Navigation">


          <ul>
            <li>
              <Link to='/'>
                <Button color="primary">Home</Button>
              </Link>
            </li>
            <li>
              <Link to='/services'>
                <Button color="primary">Services</Button>
              </Link>
            </li>
            <li>
              <Link to='/why'>
                <Button color="primary">Why BBB?</Button>
              </Link>
            </li>
            <li>
              <Link to='/schedule'>
                <Button color="primary">Schedule</Button>
              </Link>
            </li>
            <li>
              <Link to='/policies'>
                <Button color="primary">Policies</Button>
              </Link>
            </li>
            <li>
              <Link to='/contact'>
                <Button color="primary">Contact</Button>
              </Link>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}

export default App;
