import React, { Component } from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';

class FourContacts extends Component {
  render() {
    return (
          <div>
            <Grid container spacing={24}>
            <Grid item xs={3}>
              <Card>
                  <CardContent>
                    <Typography color="textSecondary" gutterBottom>
                      Visit our Office!
                    </Typography>
                    <a href="https://goo.gl/maps/zKYX8P3SE6q" target="_blank" rel="noopener noreferrer">
                      <Typography variant="h5" component="h2">
                      <i className="fas fa-map-marked-alt fa-2x"></i>
                      </Typography>
                      <Typography component="p">
                        <br />
                        106 Weir St
                        <br />
                        Taunton, MA 02780
                      </Typography>
                    </a>
                  </CardContent>
                </Card>
            </Grid>
            <Grid item xs={3}>
            <Card>
                <CardContent>
                  <Typography color="textSecondary" gutterBottom>
                    Give us a Call!
                  </Typography>
                  <a href="tel:1-508-880-3333">
                    <Typography variant="h5" component="h2">
                    <i className="fas fa-phone fa-2x"></i>
                    </Typography>
                    <Typography component="p">
                        <br />
                        <br />  
                        (508) 880-3333
                        <br />
                    </Typography>
                  </a>
                </CardContent>
              </Card>
            
            </Grid>
            <Grid item xs={3}>
            <Card>
                <CardContent>
                  <Typography color="textSecondary" gutterBottom>
                    Looking for our Fax?
                  </Typography>
                  <Typography variant="h5" component="h2">
                  <i className="fas fa-fax fa-2x"></i>
                  </Typography>
                  <Typography component="p">
                      <br />
                      <br />
                      (508) 880-3332
                      <br />
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={3}>
            <Card>
                <CardContent>
                  <Typography color="textSecondary" gutterBottom>
                    Send us an email!
                  </Typography>
                  
                  <a href="mailto:bbbautoschool@gmail.com">
                    <Typography variant="h5" component="h2">
                    <i className="fas fa-envelope-open fa-2x"></i>
                    </Typography>
                    <Typography component="p">  
                      <br />
                      <br />
                      bbbautoschool@gmail.com
                      <br />
                    </Typography>
                  </a>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </div>
    );
  }
}

export default FourContacts;
