import React, { Component } from 'react';
import Jumbotron from '../components/Jumbotron';
import JumboTronHome from '../res/top_down2.jpg';

import './css/Home.css';

import FourContacts from '../components/FourContacts';
import LogoClass from '../components/LogoClass';
import UnderConstruction from '../components/UnderConstruction';

class Home extends Component {
  render() {
    return (
      <div>
        <Jumbotron imageSource={JumboTronHome}/>
        <FourContacts />
        <LogoClass />
        <UnderConstruction />
      </div>
    );
  }
}

export default Home;
